@import url('https://fonts.googleapis.com/css?family=Poppins');
.containerlogincard{
    
    display: flex;
    justify-content: flex-start;
    align-items: center;

width: 100%;
height: 100%;
}
.containerlogincardin{
    display: flex;
    flex-direction: column;
    width:100%;
    
    margin-top: 10%;
    justify-content: center;
    margin-bottom: 10%;
    
   

}
.containerlogincardin h5{

    display: flex;

    font-family:"Poppins" ;
    font-style: normal;
    font-weight: 600px;
    font-size: 16px;
    justify-content: center;
    
}
  .login{
    display: flex;
    margin-top: 10px;
    
    width:200px;
    height: 50px;
    background-color: #1976d2;
    color: white;
    justify-content: center;
    align-items: center;
    font-family:"Poppins" ;
    font-size: 14px;
    font-weight: bold;
    line-height: 16px;
    border: solid;
    border-width:1px;
  }
  .login:hover{
      cursor: pointer;
      
  }
  .loginb{
      display: flex;
      justify-content: center;
      
  }
  .remember{
    margin-top: 20px;
    
}
.remember input {
  position: fixed;
  border:0;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
  
}
.loginfooter{
    display: flex;
    width: 100%;
    height: 10%;
    
    justify-content: space-between;
    margin-top: 20px;
}
.loginfooter1{
    display: flex;
    width: 45%;
    
    justify-content: flex-end;
}
.loginfooter1 a{
    font-family:"Poppins" ;
    font-size: 13px;
    font-weight: bold;
    color:blue;
    
    
}
.loginfooter2{
    display: flex;
    width: 45%;
    justify-content: flex-start;

}
.loginfooter2 a{
    font-family:"Poppins" ;
    font-size: 13px;
    font-weight: bold;
    color:blue;
    
    
}
.loginfooter1 a:hover{
    cursor: pointer;
    
}
.loginfooter2 a:hover{
    cursor: pointer;
    
}
.email{
    
    position: absolute;
    margin-left: 28vw;
    width:200px;
    height:50px;
    margin-top: -47px;
    background-color: white;
    align-items: center;
    justify-content: center;
    border: 10px solid #FFFFFF;
    box-sizing: border-box;
    box-shadow: 0px 0px 20px rgba(92, 111, 139, 0.12);
    border-radius: 9px;
    font-size: 13px;
}
.email1{
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.hrline{
    margin-top: 20px;
}
@media only screen and (max-width: 600px) {
    .containerlogincardin{
        width:85%;
        height: 90%;
    }
    .containerlogincardin h5{
        font-size: 11px;
    }
    .loginfooter1 p{
        font-size: 10px;
    }
    .loginfooter2 p{
        font-size: 10px;
    }
    .email{
        margin-left: 30vw;
        width:90px;
        height:50px;
        font-size: 9px;
    }
    .login{
        width:100px;
        height: 30px;
        font-size: 9px;
      }
      .hrline{
        margin-top: 10px;
    }
    .remember{
        margin-top: 10px;
        
    }
}

  @media only screen and (max-height: 650px){
    .containerlogincardin{
        display: flex;
        flex-direction: column;
        margin-top: 0%;
        width: 97%;
        
    }

    .email11{
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
    }
    .email1{

        width:100%;
        background-color: #FFFFFF;
    }
  }
    